.intro-video {
    position: absolute;
    top:0;
    left:0;
   /* z-index:100;*/
    /*height:100vh;*/
    min-height:100vh;
    width:100%;
    display:flex;
  align-items:center;
  color:#fff;
  .fullscreen-vide-wrap{
    height:100vh;
    overflow: hidden;
    position: absolute;
    top:0;
    left:0;
    width:100%;
    z-index:10;
    video{ 
        min-width:100%;
        min-height:100%;
        }
}
.header-overlay{
    height:100vh;
    position: absolute;
    top:0;
    left:0;
    width:100%;
    z-index:11;
    background:#000000;
    opacity:0.8;
  }
  .landing-logo {
    margin:auto;
    width:292px;
    height:47px;
    position:absolute;
    top:30%;
    left:50%;
    z-index:13;
    transform: translate(-50%,-30%);
    @media #{$mq-1680-up} {
        top:50%;
        transform:translate(-50%,-50%);
    }
    @media #{$mq-1000-down-landscape} {
        top:7%;
        transform: translate(-50%,-7%);
    }
    .g-logo {
        height:70px;
        width:7px;
        background:#F6921E;
        position:absolute;
        top:-13px;
        left:42px;
        bottom:0;   
        clip: rect(0px, 70px, 70px, 0px); 
        .g-reverse {
            position:absolute;
            left:15px;
            top:13px;
            
            z-index:15;
            width:40px;
            height:45px;
            overflow: hidden;
            /*transform: scaleX(0);*/
            clip: rect(0px, 0px, 45px, 0px);
        } 
       
    }
    .g-yellow {
        width:45px;
        height:45px;
        left:57px;
        top:2px;
        position: absolute;
        clip: rect(0px, 0px, 45px, 0px);
      
    }   
   
  }
  .no-g {
    margin:auto;
    width:292px;
    height:47px;
    position:absolute;
    top:30%;
    left:50%;
    transform: translate(-50%,-30%);
    opacity:0;
    z-index:17;
    @media #{$mq-1000-down-landscape} {
        top:7%;
        transform: translate(-50%,-7%);
    }
    @media #{$mq-1680-up} {
        top:50%;
        transform:translate(-50%,-50%);
    }
    
}
  .intro-content {
      max-width:600px;
      padding-left:1rem;
      padding-right:1rem;
      margin-left:auto;
      margin-right:auto;
      margin-top:auto;
      z-index:12;
      display:flex;
      height:100%;
      flex-direction: column;
      .separatorline{
          width:290px;
          height:1px;
          margin:0 auto 45px auto;
          background-repeat:no-repeat;
          opacity:.5;
          background:#fff;
      }
     
      .intro-contact-details {
          margin-top:auto;
          p{
              color:#fff;
              @include swisslight;
              font-size:13px;
              text-align:center;
              text-transform: uppercase;
              a{
                  text-decoration:none;
                  color:#fff;
                    @include swisslight;
                    font-size:13px;
                    text-align:center;
                    text-transform: uppercase;
              }
          }
          .social {
              padding-top:0px;
              padding-bottom:30px;
              width:230px;
              margin:0 auto;
              @media #{$mq-1000-down-landscape} {
              padding-bottom:15px;
            }
              ul li{
                  padding:0 10px;
                  margin:0;
                  list-style:none;
                  display: inline;
                  a{
                      width:32px;
                      height:32px;
                      display: inline-block;
                      font-size:0;
                      position: relative;
                      &:after {
                          position:absolute;
                          content:'';
                          top:0;
                          left:0;
                          width:100%;
                          height:100%;
                          background-image:url('images/social.svg');
                         
                          

                      }
                  }
                  &:nth-child(1) a{
                    &:after {
                        background-position:-64px -157px;
                    }
                  }
                  &:nth-child(2) a{
                    &:after {
                        background-position:-110px -157px;
                    }
                  }
                  &:nth-child(3) a{
                    &:after {
                        background-position:-154px -157px;
                    }
                  }
                  &:nth-child(4) a{
                    &:after {
                        background-position:-196px -157px;
                    }
                  }
              }
          }
          p.intro-copy {
              color: #fff;
              @include swisslight;
              text-align:center;
              font-size:10px;
              padding-bottom:20px;
              text-transform: uppercase;
              @media #{$mq-1000-down-landscape} {
                padding-bottom:10px;
              }
          }
      }
  }
}



  
.slide {
    
.slide_menu {
    width:35px;
    display: block;
    div{
        height:2px;
        background:#414141;
        margin:5px 0;
    }
}
}
#menu {
    width:400px;
    position: fixed;
    /*background:rgba(0,0,0,.7);*/
    background:#000;
    height:100%;
    z-index:25;
    top:0;
    bottom:0;
    right:auto;
    left:-400px;
    transition: .5s;    
    overflow-y: auto;
    
    @media #{$mq-400-down} {
        width:100vw;
    }
  ul {
      padding:0;
      margin:214px 0 0 0;
      @media #{$mq-1000-down} {
        margin:106px 0 0 0;

    }
    li {
        list-style:none;               
        padding:0;
        margin:0;        
        line-height: 31px;
        transform: translateX(-50px);
        opacity:0;
        transition: 1s; 
        text-align: right;
        @media #{$mq-1000-down} {
            line-height:20px;

        }
        a{
            transition: 1s; 
            line-height: 31px;
            text-transform:uppercase;
            font-size:60px;
            color:#fff;
            text-align: right;
            text-decoration:none;
            @include swissblackcondensed;
            @media #{$mq-1000-down} {
                font-size:30px;
                line-height:20px;

            }
            &.active {
                color:#f6921e;
            }
        }
       
    }
  }
  &.show {
      display:block;
      ul {
          li.nav-item {
              
          /*  transform: translateX(0px);    */       
          }
      }
  }
  
  a.closeMenu {
    width:35px;
    display: block;
    position:absolute;
    z-index:20;
    right:20px;
    top:40px;
    div{
        height:2px;
        background:#fff;
        margin:5px 0;
        &:nth-child(1){
            transform:rotate(45deg) translate(0px,-2px);
        }
        &:nth-child(2){
            transform:rotate(-45deg) translate(7px,-5px);
        }
    }
  }
}

