.boxes-cont {
    width:100%;
    max-width:812px;
    margin:40px auto 0 auto;

    .box-head {
        max-height:300px;
        position:relative;
        margin:0 auto;
        @media #{$mq-1000-down}{
            margin-left:40px;
            margin-right:40px;
        }
        
        &:after {
            position: absolute;
            width:100%;
            height:100%;
            top:0;
            left:0;
            background:rgba(0,0,0,.6);
            content:'';
        }
        .work-head-copy {
            position:absolute;
            top:30px;
            left:30px;
            z-index:10;
            h2 {
                font-size:50px;
                line-height:40px;
                text-transform: uppercase;
                @include swissblackcondensed;
                color:#fff;
                padding:0;
                margin:0 0 25px 0;
                @media #{$mq-1000-down}{                  
                    font-size: 32px;
                    line-height: 26px;
                    margin:0 0 14px 0;
                }
            }
            p{
                font-size:8px;
                line-height:14px;
                @include swisslight;
                color:#fff;
                padding:0;
                margin:0;
                max-width:222px;
            }
        }
        img {
            max-width:100%;
            height:auto;
        }
    }
    @media #{$mq-1000-down}{
       
        margin-top:25px;
    }
    .boxes {
        position:relative;
        display:grid;
        /*grid-template-columns: repeat(auto-fill,minmax(533px,1fr));*/
        grid-template-columns: 2fr 1fr 1fr 2fr;
        /*grid-template-columns: auto auto auto;*/
        /*grid-template-rows: minmax(100px,auto);*/
        grid-auto-flow: dense;
        grid-gap:9px;
        /*max-width:812px;
        width:100%;*/
        
        margin:9px auto 0 auto;
        
        @media #{$mq-1000-down} {
            margin: 0 40px;
            grid-template-columns: 1fr 1fr;
        }
        @media #{$mq-620-down}{
            grid-template-columns: 1fr;
            margin:9px 0 0 0;
        }
      .box{
          max-height:300px;
          .box-cont
          {
              position:relative;
              overflow: hidden;
              @media #{$mq-620-down}{
              margin:0 40px;
              }
              img {
                  width:100%;
                  height:300px;
                  object-fit: cover;
              }
              max-height:300px;
              &:hover .box-copy {
                  opacity:1;
              }
              .box-copy{
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                height: 100%;
                width: 100%;
                opacity: 0;
                transition: .5s ease;
                background: rgba(0,0,0,.6);
                display:flex;
                justify-content: flex-end;
                flex-direction: column;
                    h2{
                        @include swissBT;
                        font-size:27px;
                        line-height:34px;
                        color:#fff;
                        padding-left:21px;
                        text-transform:uppercase;

                    }
                    h3{
                        @include swissRomanCond;
                        font-size:18px;
                        line-height:24px;
                        color:#f6921e;
                        padding-left:21px;
                        padding-bottom:16px;
                        text-transform:uppercase;
                    }
                    a{
                        text-decoration:none;
                        color:#fff;
                        @include swissBT;
                        font-size:9px;
                        line-height:14px;
                        padding-left:21px;
                        padding-bottom:16px;
                        text-transform:uppercase;
                    }
              }
          }
          &.threefour {
            grid-column: span 3;
            @media #{$mq-1000-down}{
              grid-column: span 1;
            }
          }
          &.onefour {
            grid-column: span 1;
        }
      }
    }
}
.content.front-page-slide-up {
    #menu {
            transform: translateY(120px);
            background:none;
        
    }
